* {
    margin: 0;
    padding: 0;
    line-height: normal;
}

html,
body {
    height: 100vh;
    width: 1024px;
    margin: 0 auto;
    padding-bottom: 24px;
}

#root,
#root > div {
    height: 100%;
}

#header {
    height: 64px;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid gray;
}

#header-area {
    width: 1024px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#header-area img {
    width: 128px;
    height: 36px;
}

#body {
    min-height: 100vh;
    max-width: 1024px;
    width: 100%;
    margin: 0 auto;
    padding-bottom: 24px;
}

#footer {
    height: 200px;
    background-color: rgb(230, 230, 230);
}

@media screen and (max-width: 400px) {
    .product-card {
        width: 160px !important;
    }
    #header {
        padding: 0 8px;
    }
    #body {
        padding: 0 8px;
    }
    .ant-carousel {
        margin: 0 -8px;
    }
}
